body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-linear-gradient(left,orange,white,green);
  background-image: linear-gradient(to right,orange,white,green);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Locations{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.LocationIcon{
    margin: 2px;
    padding: 1px;
    border: 2px solid black;
    box-sizing: border-box;
    background-image: -webkit-linear-gradient(top left,grey,white);
    background-image: linear-gradient(to bottom right,grey,white);
}
.LocationIcon:hover{
    margin: 2px;
    padding: 1px;
    border: 2px solid black;
    box-sizing: border-box;
    background-image: -webkit-linear-gradient(top left,black,white);
    background-image: linear-gradient(to bottom right,black,white);
    -webkit-animation: Icon-bounce infinite 0.3s linear;
            animation: Icon-bounce infinite 0.3s linear;
}
.IconsPart{
    
}
.ContentPart{
    width: 65%;
    margin: 2px;
    padding: 1px;
    border: 2px solid black;
    box-sizing: border-box;
}
.titleClass{
    border-bottom: 2px solid black;
    padding: 50px;
    background-image: -webkit-linear-gradient(silver,white);
    background-image: linear-gradient(silver,white);
    color: black;
    font-size: 33px;
    font-weight: bolder;
}
.contentClass{
    display: flex;
}
.descriptionClass{
    text-align: left;
    background-image: -webkit-linear-gradient(silver,white);
    background-image: linear-gradient(silver,white);
    color: black;
    font-size: 25px;
    font-weight: lighter;
    padding-top: 20px;
    line-height: 40px;
    padding-left: 10px;
    padding-right: 10px;
}
@-webkit-keyframes Icon-bounce {
    /* from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    } */
    /* 0%, 20%, 40%, 60%, 80%, 100% {transform: translateY(0);} */
    50% {-webkit-transform: translateY(-5px);transform: translateY(-5px);}
  }
@keyframes Icon-bounce {
    /* from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    } */
    /* 0%, 20%, 40%, 60%, 80%, 100% {transform: translateY(0);} */
    50% {-webkit-transform: translateY(-5px);transform: translateY(-5px);}
  }
.App {
  text-align: center;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 30px;
  padding-top: 20px;
}
